  /* word-wrap:break-word;
    white-space:normal;
    word-break:break-all;*/
}
.font-word {
  word-wrap: break-word;
  white-space: normal;
  word-break: break-all;
}
.font-word-ellipsis {
  text-overflow: ellipsis;
}
body,
button,
