textarea {
  font: 12px/1.5 "Microsoft Yahei", "", "", "arial";
  outline: 0;
}
input::-ms-clear {
  display: none;
}
a {
  text-decoration: none;
}
a:link,
a:visited {
  outline: 0;
  text-decoration: none;
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
  -ms-transition: all 0.2s ease-in-out;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
.mmg-bodyWrapper td {
  vertical-align: middle;
}
a img,
button {
  border: none;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ol,
ul {
  list-style: none;
}
capation,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
q:before,
q:after {
  content: ' ';
}
abbr,
acronym {
  border: 0;
}
