.btn {
  height: 30px;
  line-height: 30px;
  padding: 0 12px;
  cursor: pointer;
  text-align: center;
  display: inline-block;
  border-radius: 3px;
}
.btn:hover,
.btn-icon-default:hover,
.btn-icon-blue:hover,
.btn-icon-green:hover,
.btn-icon-white:hover,
.btn-icon-orange:hover,
.btn-warning:hover,
.btn-icon-green:hover {
  opacity: .7;
  filter: alpha(opacity=70);
}
.btn-icon-text {
  display: inline-block;
}
.btn-icon-default {
  background-color: #eee;
  color: #666;
}
.btn-icon-green {
  background-color: #4eaf38;
  color: #FFFFFF;
}
.btn-icon-blue {
  background-color: #40a4ff;
  color: #FFFFFF;
}
.btn-icon-red {
  background-color: #f9653f;
  color: #FFFFFF;
}
.btn-icon-white {
  background-color: #FFFFFF;
  border: #dedede solid 1px;
  color: #666666;
}
.btn-icon-orange,
.btn-warning {
  background-color: #f3982d;
  color: #FFFFFF;
}
.btn-box .btn {
  margin: 5px 10px 5px 0;
}
