.work-flow {
  width: 100%;
  height: auto;
  background: #f3f3f3;
  border: 0px solid #fff;
  padding-top: 10px;
}
.flow-chart-con .drop-con {
  float: right;
  height: 23px;
  margin-top: -24px;
  background: #fff;
  padding: 5px 10px;
  position: relative;
  display: none;
  cursor: pointer;
}
.drop-con i {
  display: block;
  overflow: hidden;
  width: 16px;
  height: 7px;
  margin-top: 5px;
  text-indent: -999px;
  background: url('/static/image/base-new.png') -128px -82px no-repeat;
  margin-left: 5px;
  cursor: pointer;
}
.flow-chart-drop {
  display: none;
}
.flow-chart-drop .drop-con-down {
  float: right;
  height: 23px;
  margin-top: 0px;
  background: #ededed;
  padding: 2px 10px;
  position: relative;
  cursor: pointer;
}
.drop-con-down i {
  display: block;
  overflow: hidden;
  width: 16px;
  height: 7px;
  margin-top: 7px;
  text-indent: -999px;
  background: url('/static/image/base-new.png') -129px -74px no-repeat;
  margin-left: 5px;
  cursor: pointer;
}
.work-flow .main-box {
  padding: 10px 20px;
  overflow: hidden;
  position: relative;
  margin: 0 auto;
}
.work-flow .main-box .step {
  float: left;
  position: relative;
  z-index: 1;
  overflow: hidden;
}
.work-flow .main-box .step ul {
  float: left;
  overflow: hidden;
}
.work-flow .main-box .step .org-name {
  display: inline-block;
  margin-left: 5px;
  margin-top: 5px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.work-flow .main-box .step ul li {
  float: left;
  overflow: hidden;
}
.work-flow .main-box .step ul li p {
  line-height: 16px;
}
.work-flow .main-box .step .bg-border {
  border-left: 1px solid #ccc;
  border-right: 1px solid #ccc;
  position: absolute;
  left: 0;
  z-index: -1;
  clear: both;
}
.work-flow .main-box .step .node {
  /*width: 100px;*/
  height: 40px;
  text-align: center;
  float: left;
  position: relative;
  padding: 16px 8px 0;
  /* cursor: pointer;*/
}
.work-flow .main-box .step p {
  position: absolute;
  top: -2px;
  width: 100px;
  left: 50%;
  margin-left: -50px;
}
.work-flow .main-box .step .node img {
  width: 32px;
  height: 32px;
}
.work-flow .main-box .step .line {
  border-bottom: 1px solid #ccc;
  position: relative;
  float: left;
}
.node-detail {
  width: 190px;
  height: 100px;
  color: #000;
  overflow: hidden;
}
.node-detail li {
  width: 200px;
  height: 30px;
}
