  border-bottom: 1px solid #DEDEDE;
  position: relative;
}
.topbar .manager li .person-content .upload-photo .upload-img {
  margin: 28px 22px 0 38px;
}
.topbar .manager li .person-content .upload-photo .upload-img img {
  border-radius: 50%;
  border: 1px solid #dedede;
}
.topbar .manager li .person-content .upload-photo .upload-img:hover .img-modal,
.topbar .manager li .person-content .upload-photo .upload-img.hover .img-modal {
  display: block;
}
.topbar .manager li .person-content .upload-photo .case-number {
  margin-top: 18px;
}
.topbar .manager li .person-content .upload-photo .case-number .number {
  font-size: 20px;
  color: #03a9f4;
}
.topbar .manager li .person-content .upload-photo .case-number .number:hover {
  text-decoration: underline;
}
.topbar .manager li .person-content .upload-photo .case-number .title {
  color: #666666;
}
.topbar .manager li .person-content .upload-photo .img-modal {
  position: absolute;
  width: 80px;
  height: 80px;
  z-index: 22;
  top: 28px;
  left: 38px;
  border-radius: 50%;
  text-align: center;
  border: #dedede solid 1px;
  cursor: pointer;
  overflow: hidden;
  display: none;
}
.topbar .manager li .person-content .upload-photo .img-modal .modal-bg {
  width: 80px;
  height: 80px;
  background-color: #000;
  opacity: 0.4;
  filter: alpha(opacity=40);
}
.topbar .manager li .person-content .upload-photo .img-modal .title {
  position: absolute;
  width: 100%;
  height: 100%;
  line-height: 80px;
  text-align: center;
  top: 0;
  left: 0;
}
.topbar .manager li .person-content .operation-content {
  color: #666666;
  padding: 15px 28px;
}
.topbar .manager li .person-content .operation-content .person,
.topbar .manager li .person-content .operation-content .amend-password,
.topbar .manager li .person-content .operation-content .my-tool {
  width: 104px;
  text-align: center;
  cursor: pointer;
}
.topbar .manager li .person-content .operation-content .icon {
  width: 24px;
  height: 24px;
}
.topbar .manager li .person-content .operation-content .icon.icon-person {
  background-position: -135px 0;
}
.topbar .manager li .person-content .operation-content .icon.icon-password {
  background-position: -163px 0;
}
.topbar .manager li .person-content .operation-content .icon.icon-tool {
  background-position: -193px 0;
}
.topbar .manager li .person-content .operation-content p {
  margin-top: 6px;
}
.topbar .manager li .person-content .operation-content p:hover {
  color: #2d87f9;
}
.topbar .manager li .person-content .logout {
  display: block;
  width: 100%;
  padding: 6px 0;
  text-align: center;
}
.topbar .manager .message-pan {
  position: relative;
}
.topbar .manager .message-pan .iocn-pan {
  background-position: -6px -2px;
  cursor: pointer;
}
.topbar .manager .message-pan .iocn-pan:hover {
  background-position: -6px -24px;
}
.topbar .manager .message-pan .message-count {
  position: absolute;
  width: 14px;
  height: 14px;
  line-height: 14px;
  text-align: center;
  background-position: -93px -2px;
  top: -5px;
  right: 6px;
}
.topbar .manager .tooldownload .tooldownload-icon {
  background-position: -27px -2px;
}
.topbar .manager .tooldownload .tooldownload-icon:hover {
  background-position: -27px -24px;
}
.topbar .manager .volume {
  color: #b1d1fa;
  z-index: 1;
}
.topbar .manager .version {
  cursor: pointer;
}
.topbar .manager .version-icon {
  display: block;
  background: url(/static/image/head/topbar-right-icon.png) -218px 0 no-repeat;
}
.topbar .manager .version-icon:hover {
  background-position: -218px -22px;
}
/*-start*/
.img-cut {
  margin-top: 10px;
  padding: 0 10px 10px 10px;
  height: 285px;
  position: relative;
  border-bottom: 1px #dedede solid;
}
.img-cut .img-left {
  position: absolute;
  width: 356px;
