}
.img-cut .img-right {
  width: 319px;
  height: 270px;
  float: right;
}
.img-right .exp-tab {
  text-align: center;
}
.img-right .explain {
  margin-top: 15px;
}
.img-right .big-img-tab {
  width: 90px;
  height: 90px;
  margin: 20px 0 0 112px;
  overflow: hidden;
}
.img-right .small-img-tab {
  width: 30px;
  height: 30px;
  margin: 20px 0 0 142px;
  overflow: hidden;
}
.import-div {
  width: 100%;
  /*height: 45px;*/
  line-height: 45px;
  padding-top: 8px;
  border-top: solid 1px #dedede;
}
.layui-layer-btn2 {
  /*_margin-top: 10px;*/
  margin-left: 10px;
  height: 30px;
  line-height: 30px;
  width: 60px;
  padding: 2px 2px;
  color: #666666;
  cursor: pointer;
  position: absolute;
  bottom: 12px;
  left: 10px;
  text-align: center;
  overflow: hidden;
}
.back-con {
  background: url(/static/image/home/upload.png) 0 0 no-repeat;
  display: inline-block;
  width: 15px;
  height: 14px;
  margin-right: 5px;
  vertical-align: middle;
}
.span-con {
  display: inline-block;
  vertical-align: middle;
}
.Wstandard {
  width: 280px;
  max-height: 280px;
  display: block;
}
.Hstandard {
  height: 280px;
  max-width: 280px;
  display: block;
  margin: 0 auto;
}
.input-hidden {
  position: absolute;
  width: 70px;
  height: 30px;
  margin-left: -71px;
  opacity: 0;
  filter: alpha(opacity=0);
  cursor: pointer;
  margin-top: 11px;
  border: 1px solid;
}
.reupload-btn {
  height: 30px;
  min-width: 60px;
  line-height: 30px;
  margin-top: 11px;
  margin-left: 20px;
