  padding: 0px 4px;
  text-align: center;
  width: 60px;
  cursor: pointer;
}
.cancel-btn {
  margin: 11px 20px;
}
.confirm-btn {
  margin-top: 11px;
}
.reupload-btn i,
.reupload-btn span {
  line-height: 30px;
}
